    componentDidMount() {
        this._isMounted = true;
        this.props.setSpinner();

        console.log(this.state.ilce)


        //default_name

       // this.state.ilce.sort((a,b) => (a.default_name > b.default_name) ? 1 : ((b.default_name > a.default_name) ? -1 : 0))

        this.state.ilce.sort(function (a, b) {
                return a.default_name.localeCompare(b.default_name);
            });

        console.log(this.state.ilce)

        const order = localStorage.getItem('Order');
        const company = localStorage.getItem('Company');

        this.store.dispatch(setOrder(order));

        this.restApi.callApi('getSiparisData', {
            company: company,
        }).then(response => {
            const json = response.data[0];
            const jsonBig = response.data;

            let jsonPair=null;

            for(let x=0; x<response.data.length; x++){
                if(response.data[x].purchaseOrderNumber == this.props.order){
                    jsonPair = response.data[x];
                }
            }

            this.setState( {purchaseOrderNumber: jsonPair["purchaseOrderNumber"],
                shippingMethod: jsonPair["sourceShippingMethod"],
                shippingAddress: jsonPair["shippingAddress"],
                generatedAt:jsonPair["generatedAt"],
                purchaseOrderStatusId: jsonPair["purchaseOrderStatusId"],
                purchaseorderfulfillmentstatusid: jsonPair["purchaseorderfulfillmentstatusid"],
                sourceShippingMethod: jsonPair["sourceShippingMethod"],
                orderRealIdforFlxPoint:jsonPair["id"],
                items:jsonPair["items"],
                totalCost:jsonPair["totalCost"],
                barkodLink:jsonPair["barkodlink"]

            });
            this.store.dispatch(setOrderID(jsonPair["id"]));

            const sourceShippingJSON = this.state.sourceShippingMethod;
            const sourceShippingJSONObject = JSON.parse(sourceShippingJSON);


            const shippingJson = this.state.shippingAddress;
            const shippingJSONObject = JSON.parse(shippingJson);
console.log(shippingJSONObject["firstName"]);
            const itemsJson = this.state.items;
            const itemsJSONObject = JSON.parse(itemsJson);
            const itemsPair = itemsJSONObject[0];

            this.createList();

            let columnscolor = document.getElementById("Acknowledge");
            columnscolor.style.cursor = 'pointer';
            let backgroundColor=null;
            let color=null;
            if(this.state.purchaseOrderStatusId == 10){
                backgroundColor='#e1f4ea';
                color='#000';
                this.setState({
                    onaylaButtonIsDisabled: true,
                    iptalButtonIsDisabled:true,
                    SevkiyatEklendiStatus:false,
                    SevkiyatEkleButtonStatus:true,
                    sevkedildistatus:true
                })

            }
            if(this.state.purchaseOrderStatusId == 7){
                backgroundColor='#6562ac';
                color='#fff';
                this.setState({
                    onaylaButtonIsDisabled: false,
                    iptalButtonIsDisabled:false,
                    sevkedildistatus:false
                })

            }
            if(this.state.purchaseOrderStatusId == 8){
                backgroundColor='#e1f4ea';
                color='#000';
                this.setState({
                    onaylaButtonIsDisabled: true,
                    iptalButtonIsDisabled:false,
                    SevkiyatEklendiStatus:true,
                    SevkiyatEkleButtonStatus:false,
                    sevkedildistatus:false
                })
                let onaylaButtonCursor = document.getElementById("Acknowledge");
               // onaylaButtonCursor.style.cursor = 'not-allowed';
                let onaylaButtonOpacity = document.getElementById("Acknowledge");
               // onaylaButtonOpacity.style.opacity = '.5';
            }
            if(this.state.purchaseOrderStatusId == 9 || this.props.cancelStatus == 1){
                backgroundColor='#ff686b';
                color='#000';
                this.setState({
                    onaylaButtonIsDisabled: true,
                    iptalButtonIsDisabled:true,
                    SevkiyatEklendiStatus:true,
                    SevkiyatEkleButtonStatus:true,
                    sevkedildistatus:false
                })
                let onaylaButtonCursor = document.getElementById("Acknowledge");
                onaylaButtonCursor.style.cursor = 'not-allowed';
                let onaylaButtonOpacity = document.getElementById("Acknowledge");
                onaylaButtonOpacity.style.opacity = '.5';
                let iptalButtonCursor = document.getElementById("Cancel");
                iptalButtonCursor.style.cursor = 'not-allowed';
                let iptalButtonOpacity = document.getElementById("Cancel");
                iptalButtonOpacity.style.opacity = '.5';
            }

            let SASYaratılmaTarihi = this.dateTimeChange(this.state.generatedAt);

            let city1 = shippingJSONObject["city"];
            let city2 = city1.toLowerCase();
            let city3 = city2[0].toUpperCase() + city2.slice(1)
            console.log(sourceShippingJSONObject)
            this.setState({

                shippingFirstName:shippingJSONObject["firstName"],
                shippingLastName:shippingJSONObject["lastName"],
                shippingEmail:shippingJSONObject["email"],
                shippingCity:shippingJSONObject["city"],
                shippingPostal:shippingJSONObject["postal"],
                shippingCountry:shippingJSONObject["country"],
                shippingAddressLine1: shippingJSONObject["addressLine1"],
                shippingAddressLine2: shippingJSONObject["addressLine2"],
                divstatusBackgroundColor:backgroundColor,
                divstatusColor:color,
                sasYaratılmaTarih:SASYaratılmaTarihi,
                itemsQuantity:itemsPair["quantity"],
                itemsCost:itemsPair["subtotal"],
                itemsSku:itemsPair["sku"],
                itemsTitle:itemsPair["title"],
                itemsPair:itemsPair,
                defaultIl:city3,
                shippingDescription: sourceShippingJSONObject["description"],
                shippingCode: sourceShippingJSONObject["shippingCode"]
            })

            let seciliPlaka = this.state.ilPlaka.indexOf(city3)+1;


            let gecicisecilenilegoreilceliste= [];

            for(let o=0; o<this.state.ilce.length; o++){

                if(this.state.ilce[o].city_code == seciliPlaka){


                    gecicisecilenilegoreilceliste.push(this.state.ilce[o].default_name)

                }

            }



            this.setState({
                secilenilegöreilceler:gecicisecilenilegoreilceliste,
                secilenIlKod:seciliPlaka,
                secilenIl:city3
            })
            console.log(gecicisecilenilegoreilceliste)

            this.props.setSpinner();

        }).catch((err) => {
            console.log(err);
            this.props.setSpinner();
        })




        this.forceUpdate();

    }

