    SevkiyatButtonAction(){




        this.props.setSpinner();


        const company = localStorage.getItem('Company');
        let restApi = new RestApiModule();


        restApi.callApi('SevkiyatEkle', {
            orderId: this.props.order,
            company: company,
            il:this.state.secilenIl,
            ilce:this.state.secilenIlce,
            ilKod:this.state.secilenIlKod.toString(),
            itemsSku:this.state.itemsSku
        }).then(response => {


            console.log(response.shippingCode)
            console.log(response.labelLink)



            this.setState({
                SevkiyatEklendiStatus:false,
                barkodLink:response.labelLink,
                kargoNumarası:response.shippingCode,
                sevkButtonSecim:true,
                ilceSecim:true,
                ilSecim:true,
                SevkiyatEkleButtonStatus:true

            })
            this.props.setSpinner();


        }).catch((err) => {
            console.log(err);
            this.props.setSpinner();
        })




    }




    AcknowledgeButtonAction(orderId){

        this.props.setSpinner();

        const company = localStorage.getItem('Company');


        let restApi = new RestApiModule();



        restApi.callApi('onayla', {
            company:company,
            orderId: orderId
        }).then(response => {


            this.setState({
                purchaseOrderStatusId:8,
                divstatusBackgroundColor:'#e1f4ea',
                divstatusColor:'#000',
                onaylaButtonIsDisabled:true
            })
            this.props.setSpinner();
        }).catch((err) => {
            console.log(err);
            this.props.setSpinner();
        })
    }

    purchaseOrderStatus() {
        if(this.state.purchaseOrderStatusId == 10){

            return "Sevk Edildi";
        }
        if(this.state.purchaseOrderStatusId == 7){

            return "Yeni Sipariş";
        }
        if(this.state.purchaseOrderStatusId == 8){

            return "Onaylandı";
        }
        if(this.state.purchaseOrderStatusId == 9 || this.props.cancelStatus == 1){

            return "İptal Edildi";
        }

    }

    dateTimeChange(dateFromFlx){
            const formattedDate = new Date(parseInt(dateFromFlx))
            const formattedDateTime = formattedDate.getDate() + "." +((formattedDate.getMonth())+1) + "." + formattedDate.getFullYear().toString().substr(-2)+ " " + formattedDate.getHours() +  ":" + formattedDate.getMinutes();

            return formattedDateTime;
    }

    selectIlce = (e) => {
        let idx = e.target.selectedIndex;
        if(this.state.secilenilegöreilceler[idx] != "Lütfen Seçiniz"){


            this.setState({
                secilenIlce:this.state.secilenilegöreilceler[idx],
                secilenIl:this.state.defaultIl,
                SevkiyatEkleButtonStatus:false,
                sevkButtonSecim:false,
                valSelected: this.state.secilenilegöreilceler[idx]
            })
            console.log("ilce" + this.state.secilenilegöreilceler[idx])
        }

    }

    selectIl = (e) => {
        let idx = e.target.selectedIndex;



        let gecicisecilenilegoreilceliste= [];

        for(let o=0; o<this.state.ilce.length; o++){

            if(this.state.ilce[o].city_code == idx+1){


                gecicisecilenilegoreilceliste.push(this.state.ilce[o].default_name)

            }

        }

        gecicisecilenilegoreilceliste.push("Lütfen Seçiniz")
console.log(gecicisecilenilegoreilceliste)
        this.setState({
            defaultIl:this.state.il[idx].name,
            secilenilegöreilceler:gecicisecilenilegoreilceliste,
            secilenIlKod:idx+1,
            secilenIl:this.state.il[idx].name,
            ilceSecim:false,
            valSelectedIl: this.state.il[idx].name
        })

    }

    openInNewTab(url){
        const newWindow = window.open(url, '_blank', 'noopener,noreferrer')
        if (newWindow) newWindow.opener = null
    }


